html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-snap-type: mandatory;
  scroll-snap-points-y: repeat(100vh);
  scroll-snap-type: y mandatory;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-Nunito);
  background: var(--bgcolor);
  transition: all ease-in-out 500ms;
}

.container {
  scroll-snap-type: y mandatory;
}
.container > * {
  height: 100vh;
  scroll-snap-align: start;
  position: relative;
}

#footer {
  background: var(--footercolor);
  position: fixed;
  top: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  visibility: hidden;
  opacity: 0;
}
#footer.opened {
  visibility: visible;
  opacity: 1;
}

:root {
  --font-Nunito: "Nunito", sans-serif;
  --tg-textGray: hsla(0, 0%, 0%, 0.4);
  --tg-textGray-dark: rgb(174, 179, 203);
  --tg-bgcolor: hsl(0, 0%, 53%);
  --tg-bgcolor2: hsl(129, 62%, 62%);
  --bgcolor: hsl(210, 33%, 96%);
  --bgcolor-dark: hsl(230, 17%, 14%);
  --btntitle-light: hsl(0, 0%, 35%);
  --btntitle-dark: hsl(0, 0%, 80%);
  --btncolor: hsl(262, 97%, 61%);
  --btncolor-dark: hsl(262, 80%, 55%);
  --btncolor-hover: hsl(262, 100%, 55%);
  --btncolor-hover-dark: hsl(262, 85%, 48%);
  --btntext: hsl(0, 0%, 100%);
  --btntext-dark: hsl(0, 0%, 85%);
  --footercolor: rgba(0, 0, 0, 0.3);
  --footercolor-dark: rgba(0, 0, 0, 0);
  --modalbg: rgb(243, 243, 243);
  --modalbg-dark: hsl(230, 17%, 27%);
  --modalicon: hsl(129, 62%, 62%);
  --modalicon-dark: hsl(129, 55%, 62%);
  --modalheader: hsl(0, 0%, 0%);
  --modalheader-dark: hsl(0, 0%, 90%);
  --modaldesc: hsl(0, 0%, 50%);
  --modaldesc-dark: hsl(0, 0%, 76%);
  --modalbtn: hsl(0, 0%, 50%);
  --modalbtn-dark: hsl(0, 0%, 90%);
  --modalbtn-hover: hsl(0, 0%, 42%);
  --modalbtn-hover-dark: hsl(0, 0%, 80%);
  --modalbtn-text: hsl(0, 0%, 100%);
  --modalbtn-text-dark: hsl(0, 0%, 27%);
}

body.dark {
  transition: all ease-in-out 500ms;
  --tg-textGray: var(--tg-textGray-dark);
  --bgcolor: var(--bgcolor-dark);
  --btntitle: var(--btntitle-dark);
  --btncolor: var(--btncolor-dark);
  --btncolor-hover: var(--btncolor-hover-dark);
  --btntext: var(--btntext-dark);
  --footercolor: var(--footercolor-dark);
  --modalbg: var(--modalbg-dark);
  --modalicon: var(--modalicon-dark);
  --modalheader: var(--modalheader-dark);
  --modaldesc: var(--modaldesc-dark);
  --modalbtn: var(--modalbtn-dark);
  --modalbtn-hover: var(--modalbtn-hover-dark);
  --modalbtn-text: var(--modalbtn-text-dark);
}

ul.toggle-list,
li.toggle-list-item {
  z-index: 99;
  list-style: none;
  margin: 0;
  padding: 0;
}

.toggle-list {
  text-align: center;
  align-items: center;
  width: 6rem;
  position: fixed;
  top: 1rem;
  right: 5rem;
}
.toggle-list-item {
  margin: 0 2em;
}
.toggle-list-item > h4 {
  color: var(--tg-textGray);
  margin-bottom: 0.3rem;
}

.toggle {
  display: none;
}
.toggle, .toggle:after, .toggle:before, .toggle *, .toggle *:after, .toggle *:before, .toggle + .toggle-btn {
  box-sizing: border-box;
}
.toggle::selection, .toggle:after::selection, .toggle:before::selection, .toggle *::selection, .toggle *:after::selection, .toggle *:before::selection, .toggle + .toggle-btn::selection {
  background: none;
}
.toggle + .toggle-btn {
  outline: 0;
  display: block;
  width: 4em;
  height: 2em;
  margin: 0 auto;
  position: relative;
  cursor: pointer;
  user-select: none;
}
.toggle + .toggle-btn:after, .toggle + .toggle-btn:before {
  position: relative;
  display: block;
  content: "";
  width: 50%;
  height: 100%;
}
.toggle + .toggle-btn:after {
  left: 0;
}
.toggle + .toggle-btn:before {
  display: none;
}
.toggle:checked + .toggle-btn:after {
  left: 50%;
}
.toggle.toggle-skewed + .toggle-btn {
  overflow: hidden;
  transform: skew(-10deg);
  backface-visibility: hidden;
  transition: all 0.2s ease;
  font-family: sans-serif;
  background: var(--tg-bgcolor);
}
.toggle.toggle-skewed + .toggle-btn:after, .toggle.toggle-skewed + .toggle-btn:before {
  transform: skew(10deg);
  display: inline-block;
  transition: all 0.2s ease;
  width: 100%;
  text-align: center;
  position: absolute;
  line-height: 2em;
  font-weight: bold;
  color: white;
  text-shadow: 0 1px 0 var(--tg-textGray);
}
.toggle.toggle-skewed + .toggle-btn:after {
  left: 100%;
  content: attr(data-toggle-on);
}
.toggle.toggle-skewed + .toggle-btn:before {
  left: 0;
  content: attr(data-toggle-off);
}
.toggle.toggle-skewed + .toggle-btn:active {
  background: var(--tg-bgcolor);
}
.toggle.toggle-skewed + .toggle-btn:active:before {
  left: -10%;
}
.toggle.toggle-skewed:checked + .toggle-btn {
  background: var(--tg-bgcolor2);
}
.toggle.toggle-skewed:checked + .toggle-btn:before {
  left: -100%;
}
.toggle.toggle-skewed:checked + .toggle-btn:after {
  left: 0;
}
.toggle.toggle-skewed:checked + .toggle-btn:active:after {
  left: 10%;
}

.box {
  text-align: center;
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translateX(-50%);
}
.box > h1 {
  font-size: 3.5rem;
  margin-bottom: 7rem;
  color: var(--btntitle);
}
.box > button {
  all: unset;
  font: inherit;
  font-weight: bold;
  font-size: 1.25rem;
  padding: 1rem 1.5rem;
  border-radius: 1.875rem;
  background: var(--btncolor);
  color: var(--btntext);
  transition: all ease-in-out 200ms;
  cursor: pointer;
  user-select: none;
}
.box > button:hover {
  background: var(--btncolor-hover);
}

section > :not(.box) {
  height: 100vh;
  padding: 0 1.875rem;
}

.modal {
  background: var(--modalbg);
  max-width: 23.75rem;
  height: 25rem;
  border-radius: 0.625rem;
  text-align: center;
  position: relative;
  margin: 0 auto;
  top: 10rem;
  z-index: 50;
  visibility: hidden;
  opacity: 0;
  transition: all 450ms;
}
.modal > i {
  font-size: 5.625rem;
  margin-top: 4.375rem;
  color: var(--modalicon);
}
.modal > h1 {
  color: var(--modalheader);
}
.modal p {
  color: var(--modaldesc);
  padding: 0 1.5625rem;
}
.modal > button {
  all: unset;
  background: var(--modalbtn);
  font: inherit;
  font-weight: 600;
  color: var(--modalbtn-text);
  border-radius: 1.25rem;
  padding: 0.625rem 3.125rem;
  margin-top: 1.25rem;
  cursor: pointer;
}
.modal > button:hover {
  background: var(--modalbtn-hover);
}
.modal-basic {
  /* left: 50%;
  transform: translateX(-50%); */
}
.modal-basic.visible {
  visibility: visible;
  opacity: 1;
}
.modal-advance {
  /* transform: translateX(-50%); */
  transform: translateY(1.875rem);
}
.modal-advance.visible {
  transform: translateY(0);
  visibility: visible;
  opacity: 1;
}
.modal-pro {
  transform: scale(0.5) translateY(1.875rem);
  transform-origin: 0% 0%;
}
.modal-pro.visible {
  transform: scale(1) translateY(0);
  visibility: visible;
  opacity: 1;
}